	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Verona","date":"29 Aug 2001","slug":"/2001-08-29-verona/","tags":"Y2001"},"html":"<p>Mama should be in Italy by now, for her opera field trip with the executive board. She found an earlier flight for the return trip on Saturday, so we won't need a helicopter after all.</p>\n<p>Responding to the <a href=\"http://www.byte.com/documents/s=1115/byt20010824s0001/\">Byte article</a> about the lack of dual boot machines, and the question that who really wants one anyway, <a href=\"http://www.vfth.com/2001/08/28\">Al</a> responded that he likes dual booting MacOS 9 and OS X, and is looking forward to dual booting OS X.1 and Linux on his next machine. I'm just curious, Al, what do want in Linux that you don't have in OS X and <a href=\"http://www.publicsource.apple.com/projects/darwin/\">Darwin</a>, its BSD heart and soul? Especially since the <a href=\"http://elisa.utopianet.net/~rlucia/devel/darwin_ports/\">FreeBSD Ports</a> are being, er, ported to Darwin, it's going to have all the cool, fun stuff Linux has, plus the cool OS X desktop, without having to deal with an ugly Linux install.</p>"},"next":{"fields":{"title":"Danger Zone","date":"28 Aug 2001","slug":"/2001-08-28-danger-zone/"}},"previous":{"fields":{"title":"Roll Your Own","date":"30 Aug 2001","slug":"/2001-08-30-roll-your-own/"}}},{"node":{"fields":{"title":"Danger Zone","date":"28 Aug 2001","slug":"/2001-08-28-danger-zone/","tags":"Y2001"},"html":"<p>Christopher is making good progress on turning the world into a more dangerous place. He can now open all of our doors, including the front door (we have German style door handles; American style round door knobs would be somewhat safer). And he has figured out that electrical cords are to be inserted into electrical sockets, and has made it his personal mission to plug them all in.</p>"},"next":{"fields":{"title":"Too Loud to Sleep","date":"27 Aug 2001","slug":"/2001-08-27-too-loud-to-sleep/"}},"previous":{"fields":{"title":"Verona","date":"29 Aug 2001","slug":"/2001-08-29-verona/"}}},{"node":{"fields":{"title":"Too Loud to Sleep","date":"27 Aug 2001","slug":"/2001-08-27-too-loud-to-sleep/","tags":"Y2001"},"html":"<p>A thunderstorm woke me up, so I wrote up a comment on the discussion in \"Scripting News\" on <a href=\"http://shanson.editthispage.com/stories/storyReader$703\">Women and IT</a>.</p>"},"next":{"fields":{"title":"Too Loud to Think","date":"26 Aug 2001","slug":"/2001-08-26-too-loud-to-think/"}},"previous":{"fields":{"title":"Danger Zone","date":"28 Aug 2001","slug":"/2001-08-28-danger-zone/"}}}],"pathPrefix":"","first":false,"last":false,"index":980,"pageCount":1101}}

